import React, { useState } from 'react';
import ProfileStatusWithHooks from './PofileStatus/ProfileStatusWithHooks';
import style from './Profile.module.scss';


const ProfileInfo = ({profile, ...props}) => {
	let [editMode, setEditMode] = useState(false)
	const activateEditMode = () => {
		setEditMode(true)
	}
	const deactivateEditMode = () => {
		setEditMode(false)
	}

	return (<div className={style.Info}>
		<div className={style.Info__item}> {profile.fullName}</div>
		<div className={style.Info__item}> {<ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />} </div>
		<button onClick={editMode ? deactivateEditMode:activateEditMode } >{editMode ? "edit": "save"}</button>
		{editMode ? <ProfileData profile={profile} /> :<ProfileDataEdit profile={profile} /> }
	</div>
	)
}

const ProfileData = ({ profile, ...props }) => {
	return (
		<div >
			<div className={style.Info__item}>looking for a job: {profile.lookingForAJob ? "yes" : "no"}</div>
			{Object.keys(profile.contacts).map(key => {
				return <Contact contactTitle={key} contactValue={profile.contacts[key]} />
			})}
		</div>
	)
}

const ProfileDataEdit = ({ profile, ...props }) => {
	return <div>form</div>
}

const Contact = ({ contactTitle, contactValue }) => {
	return (
		<div className={style.Info__item}>{contactTitle}: {contactValue}</div>
	)
}

export default ProfileInfo;
